import React from "react"
import {NavLink} from "react-router-dom";

class SlotItem extends React.Component {

    render() {
        let game = this.props;
        return (
            <li className="games_item">
                <NavLink className="bl_book__link" to={game.gameUrl} >
                    <h2 className="games_name">{game.gameName}</h2>
                    <img className="games_img" src={game.gamePreviewImgUrl} alt={game.gameName}/>
                </NavLink>
            </li>
        )
    }
}

export default SlotItem