import React from 'react';
import { NavLink} from 'react-router-dom'

import "./menu.scss"

function Menu(props) {

    const {closeHeaderText, finishedCorrectingPersonalInformation} = props;


    function closeTopElement(){
        closeHeaderText();
        finishedCorrectingPersonalInformation();
    }


    return (
        <nav className="menu">
            <ul className="menu_list">
                <li className="menu_item">
                    <NavLink className="menu_link menu_link__work" to="/work/" onClick={(e) => closeTopElement()}>
                        <div className="menu_img"/>
                        <p className="menu_name">–†–∞–±–æ—Ç–∞</p>
                    </NavLink>
                </li>
                <li className="menu_item">
                    <NavLink className="menu_link menu_link__bonuses" to="/bonuses/" onClick={(e) => closeTopElement()}>
                        <div className="menu_img"/>
                        <p className="menu_name">–ë–æ–Ω—É—Å—ã</p>
                    </NavLink>
                </li>
                <li className="menu_item">
                    <NavLink className="menu_link menu_link__main" exact to="/" onClick={(e) => closeTopElement()}>
                        <div className="menu_img"/>
                        <p className="menu_name">–õ–æ–±–±–∏</p>
                    </NavLink>
                </li>
                <li className="menu_item">
                    <NavLink className="menu_link menu_link__tournament" to="/tournaments/" onClick={(e) => closeTopElement()}>
                        <div className="menu_img"/>
                        <p className="menu_name">–¢—É—Ä–Ω–∏—Ä—ã</p>
                    </NavLink>
                </li>
                <li className="menu_item">
                    <NavLink className="menu_link menu_link__rating" to="/rating/" onClick={(e) => closeTopElement()}>
                        <div className="menu_img"/>
                        <p className="menu_name">–†–µ–π—Ç–∏–Ω–≥</p>
                    </NavLink>
                </li>
            </ul>
        </nav>
    )
}


export default Menu