import React from "react"
import {NavLink} from "react-router-dom";

import Image from "../../../Components/Image";


import googleIcon from "../../../Components/images/google-icon.svg";
import atIcon from "../../../Components/images/at-icon.svg";
import ProfileInputName from "./ProfileInputName/ProfileInputName";
import ProfileInputFullName from "./ProfileInputFullName/ProfileInputFullName";
import ProfileInputTelegram from "./ProfileInputTelegram/ProfileInputTelegram";


function ProfileForm(props) {

    const {userName, userFullName, userEmail, userTelegram, showMainMenu, hideMainMenu} = props;

    function GoogleUser() {
        return (
            <div className="profile_form__top">
                <Image src={googleIcon} width={34} height={35} alt={"Google icon"}/>
                <div className="profile_form__block">
                    <p className="profile_form__text">–í—ã–ø–æ–ª–Ω–µ–Ω –≤—Ö–æ–¥ —á–µ—Ä–µ–∑ –∞–∫–∫–∞—É–Ω—Ç Google</p>
                    <p className="profile_form__email">{userEmail}</p>
                </div>
            </div>
        )
    }
    function SimpleUser() {
        return (
            <div className="profile_form__top">
                <Image src={atIcon} width={34} height={35} alt={"At icon"}/>
                <div className="profile_form__block">
                    <p className="profile_form__text">–í—ã–ø–æ–ª–Ω–µ–Ω –≤—Ö–æ–¥ —á–µ—Ä–µ–∑ –ª–æ–≥–∏–Ω –∏ –ø–∞—Ä–æ–ª—å</p>
                    <p className="profile_form__email">{userEmail}</p>
                </div>
            </div>
        )
    }
    return (
        <div className="profile_form__full">
            {(userEmail.includes('gmail.com')) ? <GoogleUser/> : <SimpleUser/>}
            <ProfileInputName userName={userName} showMainMenu={showMainMenu} hideMainMenu={hideMainMenu}/>
            <ProfileInputFullName userFullName={userFullName} showMainMenu={showMainMenu} hideMainMenu={hideMainMenu}/>
            <ProfileInputTelegram userTelegram={userTelegram} showMainMenu={showMainMenu} hideMainMenu={hideMainMenu}/>
            <NavLink className="btn link_changePass" to={"/change-password/"}>–ò–∑–º–µ–Ω–∏—Ç—å –ø–∞—Ä–æ–ª—å</NavLink>
        </div>
    );

}


export default ProfileForm