import React from "react"
//import {NavLink} from "react-router-dom";
import Button from "./../../Components/Button"



import "./changePass.scss"



class ChangePass extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            password :'',
            password_re:'',
            error_text: '',
            password_has_error:false
        };

    }



    checkPassword() {
        if(!this.state.password || this.state.password !== this.state.password_re) {
            this.setState({password_has_error:true});
            console.log("–ü–∞—Ä–æ–ª–∏ –Ω–µ —Å–æ–≤–ø–∞–¥–∞—é—Ç");

        }
        else {
            this.setState({password_has_error:false});
            this.setState({error_text:""});
            console.log("–ü–∞—Ä–æ–ª–∏ –°–æ–≤–ø–∞–¥–∞—é—Ç");
        }
    }

    handleChange(event) {
        const { name, value } = event.target;

        this.setState({
            [name] : value
        }, () => {
            if (name === 'password' || name === 'password_re') this.checkPassword();
        });
    }


    handleSubmit(event) {
        event.preventDefault();

        console.log("this.state.password = ", this.state.password);


        if( (this.state.password === "") || (this.state.password === undefined) ){
            this.setState({error_text:"–í–≤–µ–¥–∏—Ç–µ –Ω–æ–≤—ã–π –ø–∞—Ä–æ–ª—å, –º–∏–Ω–∏–º—É–º 8 —Å–∏–º–≤–æ–ª–æ–≤"});
            return;
        }

        if( this.state.password.length < 8 ){
            this.setState({error_text:"–ü–∞—Ä–æ–ª—å –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å –±–æ–ª—å—à–µ 8 –∑–Ω–∞–∫–æ–≤"});
            return;
        }
        if( this.state.password.length > 30 ){
            this.setState({error_text:"–ü–∞—Ä–æ–ª—å –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å –º–µ–Ω—å—à–µ 30 –∑–Ω–∞–∫–æ–≤"});
            return;
        }

        if( this.state.password !== this.state.password_re){
            this.setState({error_text:"–ü–∞—Ä–æ–ª–∏ –Ω–µ —Å–æ–≤–ø–∞–¥–∞—é—Ç, –ø–µ—Ä–µ–ø—Ä–æ–≤–µ—Ä—Ç–µ –∑–Ω–∞—á–µ–Ω–∏—è"});
            return;
        }


        if(this.state.password_has_error){
            console.log("We have problem");
            return
        }

        alert("Change Pass");
        // TODO: will submit the form here
    }


    componentDidMount() {
        this.props.showHeaderText("–°–º–µ–Ω–∏—Ç—å –ø–∞—Ä–æ–ª—å");
        this.props.functionCorrectingPersonalInformation();
    }



    handleInputFocus(){

        if(window.innerWidth < 820) {
            this.props.hideMainMenu();
            document.body.classList.add("FocusOnInput");
        }
    }
    handleInputBlur(){

        if(window.innerWidth < 820){
            this.props.showMainMenu();
            document.body.classList.remove("FocusOnInput");
        }
    }


    render(props) {





        return (

            <div className="changePass">
                <form className="changePass_form" onSubmit={(event) => this.handleSubmit(event)}>

                    <label className="label_element" htmlFor="pass">
                        <input id="pass" className="input_text input_pass" type="password" name="password" value={this.state.password}
                               onChange={(event)=>this.handleChange(event)}
                               onFocus={(event)=>this.handleInputFocus(event)}
                               onBlur={(event)=>this.handleInputBlur(event)}
                               placeholder="–ù–æ–≤—ã–π –ø–∞—Ä–æ–ª—å"/>
                    </label>
                    <label className="label_element" htmlFor="pass-confirm">
                    <input id="pass-confirm" className="input_text input_repeatPass" type="password" name="password_re"
                           value={this.state.password_re}
                           onChange={(event)=>this.handleChange(event)}
                           onFocus={(event)=>this.handleInputFocus(event)}
                           onBlur={(event)=>this.handleInputBlur(event)}
                           placeholder="–ü–æ–≤—Ç–æ—Ä–∏—Ç–µ –Ω–æ–≤—ã–π –ø–∞—Ä–æ–ª—å"/>
                           <span className="text_error">{this.state.error_text}</span>
                    </label>

                    <Button className="btn btn_changePass">–°–º–µ–Ω–∏—Ç—å –ø–∞—Ä–æ–ª—å</Button>

                </form>

            </div>
        )
    }

}


export default ChangePass;