import React from 'react';
import "./main.scss"

import classNames from 'classnames';
import HomePage from "./HomePage/HomePage"
import GamePage from "./GamePage/GamePage"

import TournamentsPage from "./TournamentsPage/TournamentsPage"
import SingleTournamentPage from "./SingleTournamentPage/SingleTournamentPage"



import InformationPage from "./InformationPage/InformationPage"
import PolicyPage from "./PolicyPage/PolicyPage"
import WorkPage from "./WorkPage/WorkPage"
import RatingPage from "./RatingPage/RatingPage"


import Registration from "./Registration/Registration"
import ChangePassword from "./ChangePass/ChangePass"


import SourcesOfCoins from "./SourcesOfCoins/SourcesOfCoins"
import Profile from "./Profile/Profile"



import {Switch, Route} from "react-router-dom"
import BonusesPage from "./BonusesPage/BonusesPage";

import NoPageFound from "./404/NoMatchPage"

function Main(props) {

    const {games, playGame, information, policy, users, userId, userName, userFullName, userEmail, userRank, userTelegram, coinsValue, ranks, functionCorrectingPersonalInformation, isUserRegistered, isUserWantNews, tournamentsActive, showPopup, showHeaderText, closeHeaderText, finishedCorrectingPersonalInformation, showMainMenu, hideMainMenu, tournaments, tournamentsValues, enterTournament, removeUserFromTournament} = props;

    const classes = classNames(
        "main_content",
        {
            "main-registred" : isUserRegistered
        }

    );

    return (
        <main className={classes}>
            <div className="container">
                    <Switch>
                        <Route exact path="/" render={() => <HomePage games={games} tournaments={tournaments} tournamentsActive={tournamentsActive}  />}  />

                        <Route exact path="/tournaments/" render={() => <TournamentsPage tournaments={tournaments} tournamentsActive={tournamentsActive}/>} />
                        <Route       path="/tournaments/:tournamentUrl" render={() => <SingleTournamentPage

                            isUserRegistered={isUserRegistered}
                            userId={userId}
                            userRank={userRank}


                            tournaments={tournaments}
                            tournamentsActive={tournamentsActive}
                            tournamentsValues ={tournamentsValues}

                            enterTournament={enterTournament}
                            removeUserFromTournament={removeUserFromTournament}

                            ranks = {ranks}/>}


                        />

                        <Route exact path="/work/" render={() => <WorkPage/>} />
                        <Route exact path="/bonuses/" render={() => <BonusesPage/>} />
                        <Route exact path="/bonuses/" render={() => <BonusesPage/>} />
                        <Route exact path="/rating/" render={() => <RatingPage/>} />
                        <Route exact path="/registration/" render={() => <Registration users = {users} />} />
                        <Route exact path="/information/" render={() => <InformationPage information = {information} />} />
                        <Route exact path="/privacy-policy/" render={() => <PolicyPage policy = {policy} />} />
                        <Route exact path="/sources-of-coins/" render={() => <SourcesOfCoins
                            policy = {policy}
                            closeHeaderText={closeHeaderText}
                            finishedCorrectingPersonalInformation={finishedCorrectingPersonalInformation}
                        />} />

                        <Route exact path="/change-password/" render={() => <ChangePassword
                            users = {users}
                            userId={userId}

                            showHeaderText={showHeaderText}
                            functionCorrectingPersonalInformation={functionCorrectingPersonalInformation}

                            showMainMenu={showMainMenu}
                            hideMainMenu={hideMainMenu}

                            />} />


                        <Route  path="/profile/" render={() => <Profile

                            ranks = {ranks}
                            coinsValue={coinsValue}
                            userName={userName}
                            userFullName={userFullName}
                            userTelegram={userTelegram}
                            userEmail={userEmail}
                            userRank={userRank}

                            isUserWantNews={isUserWantNews}
                            showPopup={showPopup}


                            showHeaderText={showHeaderText}
                            functionCorrectingPersonalInformation={functionCorrectingPersonalInformation}
                            closeHeaderText={closeHeaderText}


                            finishedCorrectingPersonalInformation={finishedCorrectingPersonalInformation}


                            showMainMenu={showMainMenu}
                            hideMainMenu={hideMainMenu}
                        />
                        }/>


                        <Route  path="/:gameUrl" render={(props) => <GamePage {...props}  games={games}  playGame={playGame} />} />
                        <Route component={NoPageFound} />
                    </Switch>
            </div>
        </main>
    )
}


export default Main