import React, {useEffect} from 'react'

import "./ranksPopup.scss"


import iconCoin from './../../Components/images/coin.svg'


function RanksPopup(props) {

    const {ranks, userRank} = props;

    useEffect(() => {
        document.body.classList.add("noScroll");
    }, []);


    const rankElements = ranks.map(function (rank) {

            let rankId = rank.id;

            if(rankId<=userRank){
                return (

                    <li className="popup_ranks__item rank_checked" key={rankId}>
                        <div className="popup_ranks__left">
                            <img className="popup_ranks__img" src={rank.urlImg} alt={rank.rank_name}/>
                            <p className="popup_ranks__name">{rank.rank_name}</p>
                        </div>
                        <div className="popup_ranks__right">
                            <p className="popup_ranks__done">–ü–æ–ª—É—á–µ–Ω</p>
                        </div>
                    </li>
                )
            }else{
                return (
                    <li className="popup_ranks__item" key={rankId}>
                        <div className="popup_ranks__left">
                            <img className="popup_ranks__img" src={rank.urlImg} alt={rank.rank_name}/>
                            <p className="popup_ranks__name">{rank.rank_name}</p>
                        </div>
                        <div className="popup_ranks__right">
                            <img className="popup_ranks__coin" src={iconCoin} alt="coin"/>
                            <p className="popup_ranks__bonus">+{rank.prize_bonus}</p>
                        </div>
                    </li>
                )
            }




        }
    );

    return (
        <div className="popup_ranks">
            <p className="popup_ranks__text">–£—Ä–æ–≤–Ω–∏</p>
            <ul className="popup_ranks__list">
                {rankElements}
            </ul>
        </div>
    );
}

export default RanksPopup;