import React, {useState} from 'react';
import {NavLink} from 'react-router-dom';
import Button from "../../Components/Button";


import TournamentItem from "./../../Main/HomePage/SliderSection/SimpleSlider/TournamentItem/TournamentItem"
import "./singleTournamentPage.scss"
import "./../../Components/seo.scss"
import './modalTournament.scss'


import coinIcon from "./../../Components/images/coin.svg"
import moneyIcon from "./../../Components/images/icon-money.png"


import parse from 'html-react-parser'
import PrizeFund from "./PrizeFund/PrizeFund";

import usersInCurrentTournament from "./../../../data/usersInCurrentTournament"
import {withRouter} from "react-router";


const SingleTournamentPage = withRouter((props) => {

    const {
        tournaments,
        tournamentsActive,
        ranks,
        tournamentsValues,
        userId,
        userRank,
        enterTournament,
        removeUserFromTournament,
        isUserRegistered
    } = props;




    const [modalStatus, setModalStatus] = useState(false);


    // const modalTournamentStatus = true;


    const currentTournamentUrl = props.match.params.tournamentUrl;
    const currentTournamentIndex = tournaments.findIndex((tournament) => tournament.tournamentUrl === currentTournamentUrl);


    const currentTournament = tournaments[currentTournamentIndex];
    const tournamentStatus = currentTournament.tournamentStatus;
    const currentTournamentId = currentTournament.id;


    // console.log(currentTournamentId, "= currentTournamentId");

    let currentTournamentIndexForPlayer = tournamentsActive.indexOf(currentTournamentId);

    // console.log(currentTournamentIndexForPlayer, "= currentTournamentIndexForPlayer");


    const tournamentRules = currentTournament.tournamentRules;
    const currentMoneyPosition = currentTournament.tournament_money_position;
    const Prize_fund = currentTournament.Prize_fund;


    function setEnterTournament() {
        /// TODO AJAX –∑–∞–ø—Ä–æ—Å –∫–æ—Ç–æ—Ä—ã–π –æ–±–Ω–æ–≤–∏—Ç –±–∞–∑—É –µ—Å–ª–∏ –û–∫ —Ç–æ –æ–±–Ω–æ–≤–ª—è–µ–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ
        enterTournament(currentTournamentId);
    }

    function leftTournament() {
        /// TODO AJAX –∑–∞–ø—Ä–æ—Å –∫–æ—Ç–æ—Ä—ã–π –æ–±–Ω–æ–≤–∏—Ç –±–∞–∑—É –µ—Å–ª–∏ –û–∫ —Ç–æ –æ–±–Ω–æ–≤–ª—è–µ–º —Å–æ—Å—Ç–æ—è–Ω–∏–µ
        removeUserFromTournament(currentTournamentIndexForPlayer);
        setModalStatus(!modalStatus);
    }

    function ButtonTournament() {


        if (!isUserRegistered) {
            return <NavLink className="btn btn_enterIntoTournament" to={"/registration/"}>–ü—Ä–∏–Ω—è—Ç—å —É—á–∞—Å—Ç–∏–µ</NavLink>;
        }


        if (tournamentStatus) {

            if ((tournamentsActive.length !== 0) && (!tournamentsActive.includes(currentTournamentId))) {
                return <div className="choseOtherTournament_block">
                    <p className="btn btn_choseOtherTournament">–í—ã —É—á–∞—Å—Ç–≤—É–µ—Ç–µ –≤ –¥—Ä—É–≥–æ–º —Ç—É—Ä–Ω–∏—Ä–µ</p>
                    <p className="text_choseOtherT__text">–ß—Ç–æ–±—ã —É—á–∞—Å—Ç–≤–æ–≤–∞—Ç—å, –¥–æ–∂–¥–∏—Ç–µ—Å—å –∑–∞–≤–µ—Ä—à–µ–Ω–∏—è –∏–ª–∏ –æ—Ç–º–µ–Ω–∏—Ç–µ —É—á–∞—Å—Ç–∏–µ –≤
                        —Ç—É—Ä–Ω–∏—Ä–µ</p>
                </div>;
            } else if (tournamentsActive.includes(currentTournamentId)) {
                return <Button className="btn btn_leftTournament" onClick={() => setModalStatus(!modalStatus)}>–ü–æ–∫–∏–Ω—É—Ç—å —Ç—É—Ä–Ω–∏—Ä</Button>;
            } else {
                return <Button className="btn btn_enterIntoTournament" onClick={setEnterTournament}>–ü—Ä–∏–Ω—è—Ç—å
                    —É—á–∞—Å—Ç–∏–µ</Button>;
            }
        } else {
            return null
        }

    }

    /// TODO –ö–∞–∫ –ü–æ–ª—É—á–∏—Ç—å –≤—ã–±–æ—Ä–∫—É —Ç–æ–ª—å–∫–æ —Ç–µ—Ö –∏–≥—Ä–æ–∫–æ–≤ 10 –∫–æ—Ç–æ—Ä—ã–µ —É—á–∞—Å—Ç–≤—É—é—Ç –≤ —Ç–µ–∫—É—â–µ–º —Ç—É—Ä–Ω–∏—Ä–µ (+ –æ—Ç—Ñ–∏–ª—å—Ç—Ä–æ–≤–∞—Ç—å –Ω–∞ —Å–µ—Ä–≤–µ—Ä–µ 10 –ª—É—á—à–∏—Ö tournamentsValues: currentTournamentId ) –∏ —Å—Ñ–æ—Ä–º–∏—Ä–æ–≤–∞—Ç—å —Ç–∞–±–ª–∏—Ü—É ?
    //// –ß—Ç–æ –±—É–¥–µ—Ç –µ—Å–ª–∏ —É—á–∞—Å—Ç–Ω–∏–∫–æ–≤ –≤ —Ç—É—Ä–Ω–∏—Ä–µ < 10 ?
    const currentTournamentPlayers = usersInCurrentTournament;


    //// TODO –î–µ–ª–∞—Ç—å –ª–∏ –µ—â—ë –æ–¥–∏–Ω –∑–∞–ø—Ä–æ–∫ –æ—Ç ID –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è —á—Ç–æ –± —É–∑–Ω–∞—Ç—å –µ–≥–æ –ø–æ–∑–∏—Ü–∏—é –≤ —Ç–µ–∫—É—â–µ–º —Ç—É—Ä–Ω–∏—Ä–µ ?

    // console.log(" tournamentsValues =", tournamentsValues);
    // console.log(" tournamentsValues[currentTournamentId] =", tournamentsValues[currentTournamentId]);

    function IconPrize() {
        if (tournamentStatus) {
            return <img className="table_t__coin" src={coinIcon} alt="–º–æ–Ω–µ—Ç—ã"/>
        } else {
            return <img className="table_t__coin" src={moneyIcon} alt="–¥–µ–Ω—å–≥–∏"/>
        }
    }

    function UserResalt() {
        if (tournamentStatus) {
            return <span className="table_t__coins">{tournamentsValues[currentTournamentId].coins}</span>
        } else {
            return <span className="table_t__coins">{tournamentsValues[currentTournamentId].hryvnia}</span>
        }
    }

    //// –ü—Ä–∏ –∑–∞–≤–µ—Ä—à–µ–Ω–∏–∏ —Ç—É—Ä–Ω–∏—Ä–∞ tournamentStatus - false /// –ù–∞ —Å–µ—Ä–≤–µ—Ä–µ —Ñ–∏—à–∫–∏ –ø—Ä–µ–≤—Ä–∞—â–∞—é—Ç—Å—è –≤ –≥—Ä–∏–≤–Ω—ã .


    return (
        <section className="single_tournament">
            <TournamentItem
                tournamentsActive={tournamentsActive}
                singleTournamentMarker={true}
                tournamentName={currentTournament.tournamentName}


                Prize_fund={Prize_fund}

                tournamentStatus={tournamentStatus}
                tournamentId={currentTournamentId}
                tournament_start={currentTournament.tournament_start}
                tournament_end={currentTournament.tournament_end}
                tournamentRules={currentTournament.tournamentRules}
                tournamentImgUrl={currentTournament.tournamentImgUrl}
                tournamentImgUrl_min={currentTournament.tournamentImgUrl_min}
            />


            <div className="bl_seo">
                <h1>{currentTournament.tournamentName}</h1>
                {parse(currentTournament.tournamentDescription)}
            </div>
            <PrizeFund Prize_fund={Prize_fund} currentMoneyPosition={currentMoneyPosition} moneyIcon={moneyIcon}/>

            <ButtonTournament/>

            {(modalStatus) ? <div className="ShadowBlock">
                <div className="modal_tournament"><p className="modal_tournament__top">–í—ã —É–≤–µ—Ä–µ–Ω—ã, —á—Ç–æ —Ö–æ—Ç–∏—Ç–µ –ø—Ä–µ–∫—Ä–∞—Ç–∏—Ç—å
                    —É—á–∞—Å—Ç–∏–µ –≤ —Ç—É—Ä–Ω–∏—Ä–µ</p><p className="modal_tournament__title">{currentTournament.tournamentName}?</p>
                    <p className="modal_tournament__text">‚Äú–í–∞—à–∏ –¥–æ—Å—Ç–∏–∂–µ–Ω–∏—è –±—É–¥—É—Ç –∞–Ω—É–ª–∏—Ä–æ–≤–∞–Ω—ã‚Äù</p>
                    <div className="modal_tournament__buttons">
                     <Button className="btn_tournament" onClick={leftTournament}><span>–¥–∞</span></Button>
                    <Button className="btn_tournament" onClick={() => setModalStatus(!modalStatus)} >–Ω–µ—Ç</Button></div>
                </div>
            </div> : null}


            <div className="tournament_rules bg-block">
                <p className="tournament_rules__text">–ü—Ä–∞–≤–∏–ª–∞ –ø–æ–ª—É—á–µ–Ω–∏–µ –≤—ã–∏–≥—Ä—ã—à–∞ –≤ —Ç—É—Ä–Ω–∏—Ä–µ</p>
                <div className="tournament_rules__list">{parse(tournamentRules)}</div>
            </div>

            <div className="table_t">
                <p className="table_t__title">–¢–∞–±–ª–∏—Ü–∞ –ª–∏–¥–µ—Ä–æ–≤ —Ç—É—Ä–Ω–∏—Ä–∞</p>
                <div className="table_t__list">

                    {tournamentsActive.includes(currentTournamentId) &&
                    <div className="table_t__item user">
                        <div className="table_t__left">
                            <span className="table_t__number">94</span>
                            <img className="table_t__img" src={ranks[userRank].urlImg} alt={ranks[userRank].rank_name}/>
                            <div className="table_t__about">
                                <p className="table_t__nick">–í—ã</p>
                                <p className="table_t__id">ID: {userId}</p>
                            </div>
                        </div>
                        <div className="table_t__right">
                            {<UserResalt/>}
                            {<IconPrize/>}
                        </div>
                    </div>
                    }

                    {/* TOP 10 of tournament */}
                    <ul className="table_t__full">
                        {
                            currentTournamentPlayers.map((el, index) => {

                                let currentCurrency;

                                if (el.tournamentsValues[currentTournamentId] !== undefined) {
                                    if (tournamentStatus) {
                                        currentCurrency = el.tournamentsValues[currentTournamentId].coins;
                                    } else {
                                        currentCurrency = el.tournamentsValues[currentTournamentId].hryvnia;
                                    }
                                } else {
                                    currentCurrency = 0;
                                }

                                return (
                                    <li className="table_t__item" key={index}>
                                        <div className="table_t__left">
                                            <span className="table_t__number">{index + 1}</span>
                                            <img className="table_t__img" src={ranks[el.rank].urlImg}
                                                 alt={ranks[el.rank].rank_name}/>
                                            <div className="table_t__about">
                                                <p className="table_t__nick">{el.name}</p>
                                                <p className="table_t__id">ID: {el.id}</p>
                                            </div>
                                        </div>
                                        <div className="table_t__right">
                                            <span className="table_t__coins">{currentCurrency}</span>
                                            {
                                                <IconPrize/>
                                            }
                                        </div>
                                    </li>
                                )
                            })
                        }
                    </ul>


                </div>
            </div>


        </section>
    )
});

export default SingleTournamentPage