import React from "react"
import SlotItem from "./SlotItem/SlotItem"

function SlotsList(props){

    const {games} = props;


    SlotsList = games.map(el => (<SlotItem

        id={el.id}
        key = {el.id}
        gameName = {el.gameName}
        gameUrl = {el.gameUrl}
        gamePlatform = {el.gamePlatform}
        gamePreviewImgUrl = {el.gamePreviewImgUrl}
        gameScreen = {el.gameScreen}
        gameScreenMiddle = {el.gameScreenMiddle}
        gameAdded = {el.gameAdded}
        gameCharacteristics = {el.gameCharacteristics}
        iframeGame = {el.iframeGame}
        gameSeo = {el.gameSeo}

    />));

    return SlotsList;
}

export default SlotsList