import React from 'react'

import './../common/reset.css'
import '../common/base.scss'

import Header from './Header/Header';
import Menu from './Menu/Menu'
import Main from './Main/Main';

import GameFrame from './GameFrame/GameFrame'

import PopupWindow from './Popups/PopupWindow';
import {BrowserRouter as Router} from "react-router-dom"


/// TODO Dirty data
import users from "./../data/users"
import ranks from "./../data/ranks"


class App extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            users : users,
            tournaments: props.state.tournaments,

            value : '',

            //// Data about some user
            userId :     users[1].id,
            coinsValue : users[1].coinsValue,
            userName:    users[1].name,
            userFullName:users[1].fullName,
            userRank:    users[1].rank,
            userEmail:   users[1].email,
            userTelegram:users[1].telegram,
      tournamentsActive: users[1].tournamentsActive,
      tournamentsValues: users[1].tournamentsValues,




            ranks : ranks,

            headerTitle:{
              isShowHeaderTitle: false,
              text: "",
            },

            popupStatus: false, //// for test popups

            whichPopupToShow: {
              infoPopup: false,
              ranksPopup: false,
              nextLevelPopup: false,
            },

            gameShowStatus: false,
            games: props.state.games,
            information: props.state.information,
            policy: props.state.policy,
            isUserRegistered: true,
            isUserTryCorrectingInfo: false,
            isUserWantNews: true,

            isMenuShow: true,
        };
    }





    headerTitleClose = () =>{
       this.setState({
           ...this.state, headerTitle: {
               isShowHeaderTitle: false,
               text: "",
           }
       });
    };

    headerTitleShow = (textForTitle) =>{
        this.setState({
            ...this.state, headerTitle: {
                isShowHeaderTitle: true,
                text: [textForTitle],
            }
        });
    };


    popupClose = () => {
        this.setState({
            popupStatus: false,
            whichPopupToShow: Object.fromEntries(
                Object.entries(this.state.whichPopupToShow).map((e) => {
                    e[1] = false;
                    return e;
                })
            )
        })
    };

    popupShow = (choosePopup) => {
        this.setState({ ...this.state, whichPopupToShow: { [choosePopup]: true } });
        this.setState({ popupStatus: true });
    };


    playGame = () => {
        this.setState({gameShowStatus: true})
    };

    closeGame = () => {
        this.setState({gameShowStatus: false})
    };

    logOut = () => {
      this.setState({isUserRegistered: false})
    };

    correctingPersonalInformation = () => {
        this.setState({isUserTryCorrectingInfo:true})
    };
    finishedCorrectingPersonalInformation= () => {
        this.setState({isUserTryCorrectingInfo:false})
    };



    showMainMenu = () => {
        this.setState({isMenuShow:true})
    };

    hideMainMenu = () => {
        this.setState({isMenuShow:false})
    };


    enterTournament = (value) => {
        this.setState(state => {
            const tournamentsActive = state.tournamentsActive.concat(value);

            return {
                tournamentsActive,
            };
        });
    };



    removeUserFromTournament = i => {
        this.setState(state => {
            const tournamentsActive = state.tournamentsActive.filter((item, j) => i !== j);
            console.log(tournamentsActive);
            return {
                tournamentsActive,
            };
        });
    };



    render() {
        // console.log("coinsValue", this.state.coinsValue );
        //console.log("coinsValue = ", this.state.coinsValue);
       ///console.log(" userEmail ", this.state.userEmail);

        return (
            <Router  basename={process.env.PUBLIC_URL + "/"}>
                <div className='wrapperBlock'>

                        {this.state.gameShowStatus ?
                            <GameFrame closeGame={this.closeGame}/>
                            :
                            <div className='wrapper'>
                                <Header
                                    headerTitle={this.state.headerTitle}
                                    showHeaderText = {this.headerTitleShow}
                                    functionCorrectingPersonalInformation = {this.correctingPersonalInformation}
                                    closeHeaderText = {this.headerTitleClose}

                                    users={users}
                                    userId={this.state.userId}
                                    coinsValue={this.state.coinsValue}
                                    userName={this.state.userName}
                                    userRank = {this.state.userRank}
                                    ranks = {ranks}


                                    showPopup={this.popupShow}
                                    isUserRegistered={this.state.isUserRegistered}

                                    isUserTryCorrectingInfo = {this.state.isUserTryCorrectingInfo}
                                    finishedCorrectingPersonalInformation = {this.finishedCorrectingPersonalInformation}

                                />
                                <Menu  closeHeaderText = {this.headerTitleClose} finishedCorrectingPersonalInformation={this.finishedCorrectingPersonalInformation} />
                                <Main
                                    games={this.state.games}
                                    information ={this.state.information}
                                    playGame={this.playGame}
                                    policy={this.state.policy}




                                    coinsValue={this.state.coinsValue}

                                    userId={this.state.userId}
                                    userName={this.state.userName}
                                    userFullName = {this.state.userFullName}
                                    userRank = {this.state.userRank}
                                    userEmail = {this.state.userEmail}
                                    userTelegram = {this.state.userTelegram}
                                    tournamentsActive={this.state.tournamentsActive}


                                    ranks = {ranks}
                                    showPopup={this.popupShow}
                                    isUserRegistered={this.state.isUserRegistered}
                                    isUserWantNews={this.state.isUserWantNews}


                                    tournaments={this.state.tournaments}
                                    tournamentsValues={this.state.tournamentsValues}

                                    enterTournament ={this.enterTournament}
                                    removeUserFromTournament={this.removeUserFromTournament}

                                    closeHeaderText = {this.headerTitleClose}
                                    showHeaderText = {this.headerTitleShow}
                                    functionCorrectingPersonalInformation = {this.correctingPersonalInformation}

                                    finishedCorrectingPersonalInformation = {this.finishedCorrectingPersonalInformation}

                                    showMainMenu={this.showMainMenu}
                                    hideMainMenu={this.hideMainMenu}



                                />
                            </div>
                        }

                        {this.state.popupStatus ? <PopupWindow
                            closePopup={this.popupClose}
                            isUserRegistered={this.state.isUserRegistered}
                            functionLogOut = {this.logOut}
                            whichPopupToShow = {this.state.whichPopupToShow}

                            users={this.state.users}
                            userId={this.state.userId}

                            userName={this.state.userName}
                            userRank = {this.state.userRank}
                            userFullName = {this.state.userFullName}
                            userFullTelegram = {this.state.userTelegram}

                            ranks = {ranks}

                        /> : null}


                </div>
            </Router>
        )
    }


}


export default App