import React from "react"
import Countdown from "react-countdown";

function Timer(props){

    const {tournamentStart, tournamentEnd} = props;

    const q100 = Date.parse(tournamentEnd) - Date.parse(tournamentStart);
    const qNow = Date.now() - Date.parse(tournamentStart);

    let procForRank  = `${ (qNow * 100 / q100).toFixed(2) }%`;

    let scope = {
        splitterStyle: {
            width: procForRank,
        }
    };

    const renderer = ({days, hours, minutes, seconds, completed }) => {
        if (completed) {
            // Render a complete state
            return (
                <span>0–¥ : 0—á : 00–º : 00—Å</span>
            );
        } else {
            // Render a countdown
            return (
                <span>{days}–¥ :{hours}—á : {minutes}–º : {seconds}—Å</span>
            );
        }
    };




    return(
        <div className="timer">
            <div className="timer_block">
                <span className="timer_name">–î–æ –∫–æ–Ω—Ü–∞ –æ—Å—Ç–∞–ª–æ—Å—å:</span>
                <p className="timer_value">
                    <Countdown date={  new Date(tournamentEnd)} zeroPadDays={2} renderer={renderer} />
                </p>
            </div>
            <div className="timer_rank">
                <div className="timer_rank__line" style={scope.splitterStyle} />
            </div>
        </div>
    )


}

export default Timer