import React from "react";
import Slider from "react-slick";


import TournamentItem from "./TournamentItem/TournamentItem"
//import TournamentList from "./TournamentList"
import "slick-carousel/slick/slick.css";



export default function SimpleSlider({tournaments, tournamentsActive}) {


    /// Show only active tournaments
    const filteredTournaments = tournaments.filter(function(el) {
        return el.tournamentStatus;
    });


    let settings = {
        dots: false,
        infinite: false,
        speed: 500,
        slidesToShow: 2.5,
        slidesToScroll: 1,
        arrows: false,
        responsive: [
            {
                breakpoint: 1200,
                settings: {
                    slidesToShow: 2,
                }
            },
            {
                breakpoint: 991,
                settings: {
                    slidesToShow: 1.5,
                }
            },
            {
                breakpoint: 650,
                settings: {
                    slidesToShow: 1.2,
                }
            },
            {
                breakpoint: 490,
                settings: {
                    slidesToShow: 1.1,
                }
            },

        ]
    };

    return (
        <Slider className="slider_all" {...settings} >
            {
                filteredTournaments.map((el) => (
                    <TournamentItem
                        id={el.id}
                        key = {el.id}
                        tournamentStatus={el.tournamentStatus}
                        tournamentName={el.tournamentName}
                        tournamentUrl={el.tournamentUrl}
                        tournament_start={el.tournament_start}
                        tournament_end={el.tournament_end}
                        tournamentImgUrl={el.tournamentImgUrl}
                        tournamentImgUrl_min={el.tournamentImgUrl_min}
                        Prize_fund={el.Prize_fund}
                        tournamentDescription={el.tournamentDescription}
                        Money_position={el.Money_position}

                        tournamentsActive={tournamentsActive}
                    />
                ))
            }
        </Slider>
    );
}
