import React from 'react'
import Button from "./../../Components/Button"
import {NavLink} from 'react-router-dom'

import "./infoPopup.scss"

import enterIcon from "./../../Components/images/info-menu/exit.svg"
import profileIcon from "./../../Components/images/info-menu/profile.svg"
import informationIcon from "./../../Components/images/info-menu/info.svg"
import coinsIcon from "./../../Components/images/info-menu/sources-of-chips.svg"


function InfoPopup(props) {

    const {isUserRegistered, closePopup, functionLogOut} = props;


    function closeModal(props){
        closePopup();
    }

    function userExit (props){
        functionLogOut();
        closePopup();
    }



    function UnRegistredUser(props) {
        return (
            <div className="popup_info">
                <nav className="popup_info__inner">
                    <Button className="item_popup" type="button">
                        <img className={"item_popup__img"} src={enterIcon} alt="–í–æ–π—Ç–∏"/>
                        <span className={"item_popup_name"}>–í–æ–π—Ç–∏</span>
                    </Button>
                    <Button className="item_popup" type="button">
                        <img className={"item_popup__img"} src={profileIcon} alt="–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è"/>
                        <span className={"item_popup_name"}>–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</span>
                    </Button>
                    <NavLink className="item_popup" to={'/information/'} onClick={closeModal}>
                        <img className={"item_popup__img"} src={informationIcon} alt="–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è"/>
                        <span className={"item_popup_name"}>–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è</span>
                    </NavLink>
                </nav>
            </div>
        )
    }

    function RegistredUser(props) {
        return (
            <div className="popup_info">
                <nav className="popup_info__inner">
                    <NavLink className="item_popup" to={'/profile/'} onClick={closeModal}>
                        <img className={"item_popup__img"} src={profileIcon} alt="–ü—Ä–æ—Ñ–∏–ª—å"/>
                        <span className={"item_popup_name"}>–ü—Ä–æ—Ñ–∏–ª—å</span>
                    </NavLink>
                    <NavLink className="item_popup" to={'/information/'} onClick={closeModal} >
                        <img className={"item_popup__img"} src={informationIcon} alt="–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è"/>
                        <span className={"item_popup_name"}>–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è</span>
                    </NavLink>
                    <NavLink className="item_popup" to={'/sources-of-coins/'} onClick={closeModal} >
                        <img className={"item_popup__img"} src={coinsIcon} alt="–ò—Å—Ç–æ—á–Ω–∏–∫–∏ —Ñ–∏—à–µ–∫"/>
                        <span className={"item_popup_name"}>–ò—Å—Ç–æ—á–Ω–∏–∫–∏ —Ñ–∏—à–µ–∫</span>
                    </NavLink>
                    <Button className="item_popup" type="button" onClick={userExit} >
                        <img className={"item_popup__img"} src={enterIcon} alt="–í—ã—Ö–æ–¥"/>
                        <span className={"item_popup_name"}>–í—ã—Ö–æ–¥</span>
                    </Button>
                </nav>
            </div>
        )
    }


    function CheckUserStatus(isUserRegistered) {

        let showModal = isUserRegistered.isUserRegistered;

        return showModal ? <RegistredUser/> : <UnRegistredUser/>
    }

    return (
        <CheckUserStatus isUserRegistered={isUserRegistered} />
    );
}

export default InfoPopup;