import React, { useState } from 'react';
import "./header.scss";
import {NavLink} from "react-router-dom";

import Button from './../Components/Button'
import classNames from 'classnames';

import coin from './../Components/images/coin.svg'
import iconMenu from "./images/icon-menu.svg"
import iconBack from "./../Components/images/icon-back.svg"

import ButtonControls from "./ButtonControls/ButtonControls"

function Header(props) {

    const {headerTitle,  closeHeaderText,
        users,
        userId,
        coinsValue,
        userName,
        userRank,



        ranks,



        isUserRegistered,
        isUserTryCorrectingInfo,
        showPopup} = props;


    const [prevCoins, setCoins] = useState(coinsValue);



    function CheckButtonControl() {


        const btnControls = <ButtonControls
            isUserRegistered={isUserRegistered}
            users={users}
            userId={userId}
            ranks={ranks}
            userName={userName}
            userRank={userRank}
        />;
        const btnReturnFromCorrecting = <Button className="btn_returnFromCorrecting" href="/" onClick={(e) => closeHeaderText()} >
            <img src={iconBack} alt="return"/>
        </Button>;

        return !isUserTryCorrectingInfo ? btnControls : btnReturnFromCorrecting
    }

    const classes = classNames(
        "header",
        {
            "header-registred" : isUserRegistered
        }

    );

    return (
        <header className={classes}>

            <div className="header_controls header_userControls">
                <div className="container">
                    {/*<div onClick={() => setCoins(prevCoins + 1)}>Add coin</div>*/}

                    <CheckButtonControl isUserTryCorrectingInfo={isUserTryCorrectingInfo} />

                    {headerTitle.isShowHeaderTitle && <h1 className="header_title">{headerTitle.text}</h1>}


                    <div className="header_userInfo">
                        {
                            !isUserTryCorrectingInfo && <div className="coin_block" onClick={(e) => showPopup("nextLevelPopup")}>
                                    <img className="coin_img" src={coin} alt="coin"/>
                                    <span className="coin_value">{prevCoins}</span>
                                </div>

                        }

                        <Button className="btn_navigation" onClick={(e) => showPopup("infoPopup")}>
                            <img src={iconMenu} alt="navigation"/>
                        </Button>
                    </div>

                </div>
            </div>
            {isUserRegistered ? null :
                <div className="header_controls header_authorization">
                    <div className="container">
                        <Button className="btn btn_enter" onClick={() => {
                            console.log("enter");
                        }}><span>–í–æ–π—Ç–∏</span></Button>
                        <NavLink className="btn btn_registration" to='/registration/'>–ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞—Ç—å—Å—è</NavLink>
                    </div>
                </div>
            }



        </header>
    )
}


export default Header