import React from 'react';

import SliderSection from './SliderSection/SliderSection'
import SlotsBlock from './SlotsList/SlotsBlock'
import Proposal from './Proposal/Proposal'


import "./homepage.scss"

function HomePage(props) {


    const {games, tournaments, tournamentsActive} = props;


    return (
        <div className={"home_space"}>
            <SliderSection tournaments={tournaments} tournamentsActive={tournamentsActive}/>
            <SlotsBlock games={games}/>
            <Proposal/>
        </div>
    )

}


export default HomePage