import React from 'react';
import Button from './../../Components/Button'

import "./games.scss"
import parse from 'html-react-parser'
import {withRouter} from "react-router-dom";


const GamePage = withRouter((props) => {
    const {games, playGame,} = props;
    const currentGameUrl = props.match.params.gameUrl;
    const currentGameIndex = games.findIndex(item=>item.gameUrl === currentGameUrl);
    const game = games[currentGameIndex];

    return (
        <div className="game_block">
            <div className="game_block__top">
                <picture className="game_picture">
                    <source media="(max-width: 768px)" srcSet={game.gameScreenMiddle}/>
                    <img src={game.gameScreen} alt={game.gameName}/>
                </picture>
                <div className="game_block_navigation">
                    <h1 className="game_name">{game.gameName}</h1>
                    <p className="game_platform">{game.gamePlatform}</p>
                    <Button className="btn btn_playGame" onClick={playGame}>–ò–≥—Ä–∞—Ç—å</Button>
                </div>
            </div>
            <div className="game_info">
                <p className="game_info__title">–•–∞—Ä–∞–∫—Ç–∏—Ä–∏—Å—Ç–∏–∫–∏</p>
                <ul className="game_info__all">
                    <li className="game_info__item" key={1}>
                        <span>–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –ª–∏–Ω–∏–π</span>
                        <span>{game.gameCharacteristics.lines}</span>
                    </li>
                    <li className="game_info__item" key={2}>
                        <span>–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –±–∞—Ä–∞–±–∞–Ω–æ–≤</span>
                        <span>{game.gameCharacteristics.drums}</span>
                    </li>
                    <li className="game_info__item" key={3}>
                        <span>–ú–∏–Ω–∏–º–∞–ª—å–Ω–∞—è —Å—Ç–∞–≤–∫–∞</span>
                        <span>{game.gameCharacteristics.minBet}</span>
                    </li>
                    <li className="game_info__item" key={4}>
                        <span>–ú–∞–∫—Å–∏–º–∞–ª—å–Ω–∞—è —Å—Ç–∞–≤–∫–∞</span>
                        <span>{game.gameCharacteristics.maxBet}</span>
                    </li>
                    <li className="game_info__item" key={5}>
                        <span>–†–∏—Å–∫ –∏–≥—Ä–∞</span>
                        <span>{game.gameCharacteristics.riskGame}</span>
                    </li>
                    <li className="game_info__item" key={6}>
                        <span>–§—Ä–∏-—Å–ø–∏–Ω—ã</span>
                        <span>{game.gameCharacteristics.freeSpins}</span>
                    </li>
                    <li className="game_info__item" key={7}>
                        <span>–ë–æ–Ω—É—Å–Ω–∞—è –∏–≥—Ä–∞</span>
                        <span>{game.gameCharacteristics.bonusGame}</span>
                    </li>
                </ul>
            </div>

            <div className="game_seo">
                {parse(game.gameSeo)}
            </div>

        </div>

    )
});

export default GamePage;