import React from "react"
import Button from "../../../../Components/Button";
import correctingIcon from "../../../../Components/images/icon-correcting.svg";
import checkIcon from "../../../../Components/images/icon-checkbox-green.png"

class ProfileInputFullName extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            isInputCorrecting: false,
            inputValue: this.props.userFullName,
            error_text: '',
        };
        this.handleValueChange = this.handleValueChange.bind(this);
    }


    inputCorrecting (event){
        event.preventDefault();
        this.setState({isInputCorrecting: true});
    }

    inputFinishCorrecting (event){
        event.preventDefault();
        this.setState({isInputCorrecting: false})
    }

    handleValueChange(event) {
        this.setState({inputValue: event.target.value});
    }



    handleFocus(){

        if(window.innerWidth < 820) {
            this.props.hideMainMenu();
            document.body.classList.add("FocusOnInput");
        }



    }
    handleBlur(){

        if(window.innerWidth < 820){
            this.props.showMainMenu();
            document.body.classList.remove("FocusOnInput");
        }


        let funcClear = clearingErrorText.bind(this);
        function clearingErrorText(){
            setTimeout(
                () => {
                    this.setState({error_text: ""});
                },
                1500
            )
        }

        if (this.state.inputValue === this.props.userFullName){
            this.setState({isInputCorrecting: false});

        } else if ( (this.state.inputValue.length <= 4) ){
            this.setState({ inputValue: this.props.userFullName });
            this.setState({isInputCorrecting: false});
            this.setState({error_text:"–ü–æ–ª–Ω–æ–µ –∏–º—è –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å < 4 —Å–∏–º–≤–æ–ª–æ–≤"});
            funcClear();
        } else if( (this.state.inputValue.length > 50) ){
            this.setState({ inputValue: this.props.userFullName });
            this.setState({isInputCorrecting: false});
            this.setState({error_text:"–ü–æ–ª–Ω–æ–µ –∏–º—è –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å > 50 —Å–∏–º–≤–æ–ª–æ–≤"});
            funcClear();
        } else{
            this.setState({isInputCorrecting: false});


            // TODO –û–Ω–æ –¥–æ–ª–∂–Ω–æ –ø–æ–º–µ–Ω—è—Ç—å –∏–º—è –∏ –≤ –¥—Ä—É–≥–∏—Ö –∫–æ–º–æ–ø–Ω–µ–Ω—Ç–∞—Ö –ø–æ—Å–ª–µ —É–¥–∞—á–Ω–æ–≥–æ –æ—Ç–≤–µ—Ç–∞ —Å–µ—Ä–≤–µ—Ä–∞
            alert('–û—Ç–ø—Ä–∞–≤–ª–µ–Ω–Ω–æ–µ –ü–æ–ª–Ω–æ–µ –∏–º—è: ' + this.state.inputValue);
        }
    }


    render (){



        return(
            <form className="profile_form">
                <label htmlFor="userFullName" className="label_element">

                    {
                        this.state.isInputCorrecting
                            ?
                            <input id="userFullName" className="profile_form__input"
                                   type="text"
                                   name="name"
                                   value={this.state.inputValue}
                                   onChange={(e) => this.handleValueChange(e)}
                                   autoFocus
                                   onBlur={(e) => this.handleBlur(e)}
                                   onFocus={(e) => this.handleFocus(e)}/>
                            : <p className="profile_form__input">{(this.state.inputValue) ? this.state.inputValue : "–ü–æ–ª–Ω–æ–µ –∏–º—è –Ω–µ —É–∫–∞–∑–∞–Ω–æ"}</p>

                    }


                    <span className="text_error">{this.state.error_text}</span>
                </label>

                {
                    this.state.isInputCorrecting
                        ?
                        <div className="profile_form__change"><img src={checkIcon} alt="–ø—Ä–∏–º–µ–Ω–∏—Ç—å"/></div>
                        :
                        <Button className="profile_form__change" type="button" onClick={(e) => this.inputCorrecting(e)}>
                            <img src={correctingIcon} alt="–∏–∑–º–µ–Ω–∏—Ç—å"/>
                        </Button>
                }
            </form>
        );
    }
}

export default ProfileInputFullName