import React from "react";


import {NavLink} from "react-router-dom"
import defaultAvatar from "./../../../App/Components/images/rank/0.png"
//import Button from "../../Components/Button";




function UnRegisteredUser() {


    return (

        <div className="user_control" >
            <img className="user_control__img" width="40" height="40" src={defaultAvatar} alt="unregistred"/>
            <span className="user_control__name">Nickname</span>
        </div>

    )
}

function RegisteredUser(props) {

    const {  ranks,  userName, userRank } = props;



    const userAvatarUrl = ranks[userRank].urlImg;
    const userRankName = ranks[userRank].rank_name;


    return (
        <div className="user_control">
            <NavLink className={"user_control"} to={"/profile/"}>
                <img className="user_control__img" src={userAvatarUrl} alt={userRankName} width="40" height="40"/>
                <span className="user_control__name">{userName}</span>
            </NavLink>
        </div>
    )

}


function ButtonControls(props) {



    return (props.isUserRegistered ?
        <RegisteredUser
            userName={props.userName}
            ranks={props.ranks}
            userRank={props.userRank}


        /> : <UnRegisteredUser />)
}

export default ButtonControls;