import React from "react"
import "./prizeFund.scss"


function PrizeFund({Prize_fund, currentMoneyPosition, moneyIcon}) {

    return(
        <div className="prizeFund bg-block">
            <h2 className="prizeFund_title">–ü—Ä–∏–∑–æ–≤–æ–π —Ñ–æ–Ω–¥</h2>
            <img className="prizeFund_img" src={moneyIcon} alt="money"/>
            <span className="prizeFund_sum">{Prize_fund}</span>
            <p className="prizeFund_text">–ü—Ä–∏–∑–æ–≤—ã–µ –º–µ—Å—Ç–∞</p>
            <div className="prizeFund_list">
                {Object.keys(currentMoneyPosition).map((key) => {
                    return (
                        <div className="prizeFund_item" key={key}>
                            <div className="prizeFund_number">{key}</div>
                            <div className="prizeFund_value">{currentMoneyPosition[key]} ‚Ç¥</div>
                        </div>
                    );
                })}
            </div>
        </div>
    )
}

export default PrizeFund