import React, {useEffect} from "react"
import Button from "../../Components/Button";
import {NavLink} from "react-router-dom";
import ModulNewsletters from "../Registration/ModuleNewsletters/ModuleNewsletters"


import coinIcon from "./../../Components/images/coin.svg"
import emptyRankImg from "./../../Components/images/rank/0.png"


import "./profile.scss"
import ProfileForm from "./ProfileForm/ProfileForm";
import ProfileProgressbar from "./ProfileProgressbar/ProfileProgressbar"

function Profile(props) {
    const { ranks,


        coinsValue,
        userName,
        userFullName,
        userTelegram,
        userEmail,
        userRank,


        isUserWantNews,
        showPopup,
        showHeaderText,
        functionCorrectingPersonalInformation,
        closeHeaderText,
        finishedCorrectingPersonalInformation,

        showMainMenu,
        hideMainMenu,

    } = props;




    let rankImg = ranks[userRank].urlImg;
    if (rankImg === undefined) {
        rankImg = emptyRankImg
    }
    const rankName = ranks[userRank].rank_name;


    const rankCoinsToNextLevel = ranks[userRank].need_score;

    let prevRankId = ranks[userRank - 1];
    let prevRankImg;
    if (prevRankId === undefined) {
        prevRankImg = emptyRankImg
    } else {
        prevRankImg = prevRankId.urlImg;
    }

    const maxRank = 10;

    let nextRankId = userRank + 1;
    let nextRankImg;
    if (nextRankId > maxRank) {
        nextRankImg = emptyRankImg
    } else {
        nextRankImg = ranks[nextRankId].urlImg;
    }


    useEffect(() => {
        showHeaderText("–ê–∫–∫–∞—É–Ω—Ç –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è");
        functionCorrectingPersonalInformation();
    }, []);


    function closeTopElement(){
        closeHeaderText();
        finishedCorrectingPersonalInformation();
    }


    return (
        <div className="profile">
            <div className="profile_top">
                <img className="profile_top__img" src={rankImg} alt={rankName}/>
                <p className="profile_top__name">{userName}</p>
                <p className="profile_top__rank">{rankName}</p>
            </div>

            <div className="profile_middle">

                <div className="profile_rank">
                    <Button className="profile_rank__btn" onClick={(e) => showPopup("ranksPopup")}>
                        <img className="profile_rank__other profile_rank__prev" src={prevRankImg}
                             alt="–ü—Ä–µ–¥—ã–¥—É—â–∏–π —Ä–∞–Ω–≥"/>
                    </Button>
                    <ProfileProgressbar coinsValue={coinsValue} rankCoinsToNextLevel={rankCoinsToNextLevel}/>
                    <Button className="profile_rank__btn" onClick={(e) => showPopup("ranksPopup")}>
                        <img className="profile_rank__other profile_rank__next" src={nextRankImg} alt="–°–ª–µ–¥—É—é—â–∏–π —Ä–∞–Ω–≥"/>
                    </Button>
                </div>


                <div className="profile_coins">
                    <img className="profile_coins__img" src={coinIcon} alt="coins"/>
                    <div className="profile_coins__line">
                        {coinsValue}
                        <NavLink className="profile_coins__link" onClick={(e) => closeTopElement()} to={'/sources-of-coins/'} />
                    </div>
                </div>
            </div>

            <div className="profile_bottom">
                <ProfileForm
                    userName={userName}
                    userFullName={userFullName}
                    userEmail={userEmail}
                    userTelegram={userTelegram}
                    showMainMenu={showMainMenu}
                    hideMainMenu={hideMainMenu}
                />
                <ModulNewsletters isUserWantNews={isUserWantNews}/>
            </div>

        </div>


    )


}

export default Profile

