import React, {useState, useEffect} from "react"
import "./profileProgressbar.scss"



function ProfileProgressbar(props) {

    const {coinsValue, rankCoinsToNextLevel} = props;
    const [data, setData ] = useState(coinsValue);

    useEffect(()=>{setTimeout(function () {
        setData(coinsValue)
    }, 500)},[]);

    return (
        <div className="profile_rank__elem">
            <p className="profile_rank__text">–ü–æ–≤—ã—à–µ–Ω–∏–µ —á–µ—Ä–µ–∑:</p>
            <div className="progress">
                <progress max={rankCoinsToNextLevel} value={data} data-value={Math.ceil((data*100)/rankCoinsToNextLevel) } />
                <div className="progress-bg">
                    <div className="progress-bar"/>
                </div>
            </div>



        </div>
    )
}

export default ProfileProgressbar