import React from 'react'
import Button from "./../Components/Button"


import Video from "./../Components/images/rotate.mp4"

import "./frame.scss"


function GameFrame(props) {

    const {games} = props;


    return (
        <main>


            <div className="game_frame__inner">
                iframe with game
            </div>

            <Button className="btn_closeGame" onClick={e => props.closeGame()}>
                <svg className="btn_closeGame__icon" xmlns="http://www.w3.org/2000/svg" x="0" y="0"
                     width="20" height="20" viewBox="0 0 12 12">
                    <path fill={"#3b2d62"}
                          d="M11 .8c.3.3.3.8 0 1.2l-8.8 8.8c-.3.3-.8.3-1.2 0-.3-.3-.3-.8 0-1.2L9.8.8c.3-.3.8-.3 1.2 0z"/>
                    <path fill={"#3b2d62"}
                          d="M1 .8c.3-.3.8-.3 1.2 0L11 9.6c.3.3.3.8 0 1.2-.3.3-.8.3-1.2 0L1 2C.7 1.7.7 1.2 1 .8z"/>
                </svg>
            </Button>

            <div className="game_frame__plug">
                <video className="plug_video"
                       src={Video}
                       loop={true}
                       autostart={true}
                       autoPlay={true}
                       type="video/mp4"/>
            </div>
        </main>
    )
}


export default GameFrame