import React from "react"
import {NavLink} from "react-router-dom";
import classNames from 'classnames';
import Timer from "./Timer/Timer"


function TournamentItem(props) {

    const tournament = props;

    const singleTournamentMarker = props.singleTournamentMarker;
    const tournamentsActive = props.tournamentsActive;

    const tournamentUrl = tournament.tournamentUrl;
    const address = "/tournaments/" + tournamentUrl;

    const tournamentId = tournament.id;

    const tournamentImgUrl = tournament.tournamentImgUrl;
    const tournamentImgUrlMin = tournament.tournamentImgUrl_min;

    const tournamentStatus = tournament.tournamentStatus;

    const tournamentName = tournament.tournamentName;
    const tournamentPrize = tournament.Prize_fund;
    const tStart = tournament.tournament_start;
    const tEnd = tournament.tournament_end;


    let tournamentStart = new Date(tStart);
    let tournamentEnd = new Date(tEnd);

    let options = {
        day: 'numeric',
        month: 'long',
    };

    const itemStyles = classNames("tournament_item", {'finished': !tournamentStatus});



    function ButtonTournament() {
        if (tournamentStatus) {

            if (tournamentsActive.includes(tournamentId)) {
                return <p className="btn link_tournament__active">–í—ã —É—á–∞—Å—Ç–≤—É–µ—Ç–µ</p>;
            } else {
                return <p className="btn link_tournament">–ü—Ä–∏–Ω—è—Ç—å —É—á–∞—Å—Ç–∏–µ</p>;
            }
        }
        return <p className="tournament_end">—Ç—É—Ä–Ω–∏—Ä –∑–∞–≤–µ—Ä—à–µ–Ω</p>;
    }

    function TournamentInner() {

        return (
            <div className="tournament_itemFull">
                <picture className="tournament_pic">
                    <source media="(max-width: 400px)" srcSet={tournamentImgUrlMin}/>
                    <img className="tournament_img" src={tournamentImgUrl} alt={tournamentName} width={650}
                         height={470}/>
                </picture>

                <div className="tournament_wrapper">
                    <p className="tournament_dat">{tournamentStart.toLocaleString("ru", options)} - {tournamentEnd.toLocaleString("ru", options)}</p>
                    <div className="tournament_inner">
                        <p className="tournament_name">—Ç—É—Ä–Ω–∏—Ä <span>{tournamentName}</span></p>
                        <div className="tournament_center">
                            <p className="tournament_text">–ø—Ä–∏–∑–æ–≤–æ–π —Ñ–æ–Ω–¥</p>
                            <p className="tournament_value">{tournamentPrize}</p>
                        </div>
                        {
                            (!singleTournamentMarker) ? <ButtonTournament/> : null
                        }
                    </div>
                    <Timer tournamentStart={tStart} tournamentEnd={tEnd}/>
                </div>
            </div>
        )
    }

    function TournamentCard() {
        if(!singleTournamentMarker){
            return <NavLink className={itemStyles} to={address}><TournamentInner/></NavLink>
        } else{
            return <div className={itemStyles}><TournamentInner/></div>
        }
    }

    return (
        <TournamentCard/>
    )
}

export default TournamentItem