import React from "react";
import TournamentItem from "./TournamentItem/TournamentItem";

function TournamentList(props) {

    const {tournaments} = props;

    return (

        tournaments.map((el) => (

            <TournamentItem
                id={el.id}
                key = {el.id}
                tournamentStatus={el.tournamentStatus}
                tournamentName={el.tournamentName}
                tournamentUrl={el.tournamentUrl}
                tournament_start={el.tournament_start}
                tournament_end={el.tournament_end}
                tournamentImgUrl={el.tournamentImgUrl}
                tournamentImgUrl_min={el.tournamentImgUrl_min}
                Prize_fund={el.Prize_fund}
                tournamentDescription={el.tournamentDescription}
                Money_position={el.Money_position}

                tournamentsActive={props.tournamentsActive}
            />

        ))

    )
}

export default TournamentList